 @font-face {
  font-family: Bushido;
  src: url(../fonts/jupan.ttf);
}

.homePage {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgb(255, 255, 136);
  background-image: url(https://github.com/RavenVol/sudoku/blob/master/public/pic/samurai.png?raw=true);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-clip: border-box;
  background-size: contain;

  font-family: Bushido;
}

.curtain {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 136, 1);
  color: transparent;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "title . menu";
  justify-items: center;
  align-items: center;

  animation: curtain 2s linear 1s;
  animation-fill-mode: forwards;
}

@keyframes curtain {
  0% {
    background-color: rgba(255, 255, 136, 1);
    color: transparent;
  }
  50% {
    background-color: rgba(255, 255, 136, 0);
    color: transparent;
  }
  70% {
    background-color: rgba(255, 255, 136, 0);
    color: #000;
  }
  85% {
    background-color: rgba(255, 255, 136, 0);
    color: #b40a0a;
  }
  100% {
    background-color: rgba(255, 255, 136, 0);
    color: #000;
  }
}


.title {
  grid-area: title;
  justify-self: end;
  text-align: end;
}

.gameName {
  font-size: 20vh;
}

.author {
  font-size: 3vh;
}

.homeMenu {
  width: 100%;
  height: 33%;
  grid-area: menu;
  justify-self: start;
  position: relative;
}

.homeMenuBtn1,
.homeMenuBtn2,
.homeMenuBtn3 {
  width: 30%;
  height: 30%;
  box-sizing: border-box;
  border: 2px solid #000;
  border-radius: 60% 0 60% 0;
  background-color: rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #000;

  position: absolute;
  font-size: 3.5vh;
}

.homeMenuBtn1:hover,
.homeMenuBtn2:hover,
.homeMenuBtn3:hover {
  background-color: rgba(95, 158, 160, 0.3);
}

.homeMenuBtn1 {
  top: 0px;
  right: -30%;
  animation: buttonMove 0.3s linear 3.05s;
  animation-fill-mode: forwards;
}

.homeMenuBtn2 {
  top: 38%;
  right: -30%;
  animation: buttonMove 0.3s linear 3.1s;
  animation-fill-mode: forwards;
}

.homeMenuBtn3 {
  top: 76%;
  right: -30%;
  animation: buttonMove 0.3s linear 3.15s;
  animation-fill-mode: forwards;
}

@keyframes buttonMove {
  0% {
    right: -25%;
  }
  70% {
    right: 90%;
  }
  100% {
    right: 50%;
  }
}

