.about {
  perspective: 1000px;
}

.article {
  width: 90%;
  margin: 2.7% auto;
  background-color: rgba(255, 255, 255, 0.65);
  border-radius: 20px;
  padding: 2.3%;

  transform: rotateX(90deg);

  animation: aboutArticleMove 0.8s ease-in-out 1s;
  animation-fill-mode: forwards;
}

@keyframes aboutArticleMove {
  0% {
    transform: rotateX(90deg);
  }
  100% {
    transform: rotateX(0deg);
  }
}

.preambula, .rules {
  text-align: justify;
  margin-top: 1.2%;
}

