.header {
  position: relative;
  width: 100%;
  height: 9.6%;
  background-color: transparent;
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-items: center;

  font-family: Bushido;
  font-size: 3.5vh;
}

.menu {
  position: absolute;

  margin: 0 auto;
  width: 19.6%;
  min-width: 190px;
  height: 100%;
  box-shadow: 0 0 5px #000;
  background-color: transparent;
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-items: center;

  top: -110%;
  animation: headerMenuMove 0.3s linear 1s;
  animation-fill-mode: forwards;
}

@keyframes headerMenuMove {
  0% {
    top: -110%;
  }
  100% {
    top: 0;
  }
}

.menuBtn {
  box-sizing: border-box;
  width: 33.333%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  background-color: rgba(0, 0, 0, 0.5);
  text-decoration: none;
  color: #fff;
  border: 2px solid grey;
  transition: all 300ms linear;
}

.menuBtn:hover {
  background-color: rgba(95, 158, 160, 0.5);
  color: #fff;
}

.active {
  background-color: rgba(160, 95, 114, 0.5);
  color: #fff;
}

.menuBtn-active {
  background-color: lightblue;
  color: #fff;
}
